*{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}
.flex {
  display: -webkit-flex;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.start {
  justify-content: flex-start;
  align-items: center;
}
body {
  background-color: #552e5a;
  color: white;
  margin: 50px;
}
h2 {
  max-width: 300px;
}
.options {
  border-radius: 2px;
  background-color: red;
  padding: 5px;
}